.content{
    width: 100%;
    height: 100vh;
    background: 
        -webkit-linear-gradient(rgba(250,250,250, 0.5), rgba(255, 255, 255, 0.5)),
        -webkit-linear-gradient(290deg, rgba(255,255,255, 0.28), rgba(177, 177, 176, 0.15)),
        url('./vcl.png') no-repeat left center,
        url('./logos.png') no-repeat right 6vh bottom 3vh,
        url('./vtr.png') no-repeat right top,
        url('./vbl.png') no-repeat left bottom,
        -webkit-linear-gradient(rgb(255,255,255), rgb(255, 255, 255));
    background: 
        -o-linear-gradient(rgba(250,250,250, 0.5), rgba(255, 255, 255, 0.5)),
        -o-linear-gradient(290deg, rgba(255,255,255, 0.28), rgba(177, 177, 176, 0.15)),
        url('./vcl.png') no-repeat left center,
        url('./logos.png') no-repeat right 6vh bottom 3vh,
        url('./vtr.png') no-repeat right top,
        url('./vbl.png') no-repeat left bottom,
        -o-linear-gradient(rgb(255,255,255), rgb(255, 255, 255));
    background: 
        linear-gradient(rgba(250,250,250, 0.5), rgba(255, 255, 255, 0.5)),
        linear-gradient(160deg, rgba(255,255,255, 0.28), rgba(177, 177, 176, 0.15)),
        url('./vcl.png') no-repeat left center,
        url('./logos.png') no-repeat right 6vh bottom 3vh,
        url('./vtr.png') no-repeat right top,
        url('./vbl.png') no-repeat left bottom,
        linear-gradient(rgb(255,255,255), rgb(255, 255, 255));
    background-size: 
        100%,
        100%,
        contain, 
        550px auto, 
        25vmax auto, 
        25vmax auto,
        100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}
.center-content {
    text-align: center;
    margin-top: -150px;
}
.center-content h1{
    color: #0A6CB5;
    font-size: 94px;
    margin: 25px 0;
}

.center-content img{
    width: 50%;
}
.center-content button{
    margin: 0 5px;
    border: none;
    font-size: 50px;
    padding: 30px;
    color: #fff;
    background: #2FA8E0;
    border-radius: 7px;
    -webkit-box-shadow: 0 2px 2px rgba(0,0,0,0.24);
            box-shadow: 0 2px 2px rgba(0,0,0,0.24);
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
    cursor: pointer;
    outline: none;
}
.center-content button:hover{
    background: #0A6CB5;
}


