.qr-scanner{
    width: 40%;
    padding-top: 50px;
}

.qr-content{
    width: 100%;
    height: 100vh;
    /* margin: auto; */
    background: rgba(0,0,0,0.9);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
} 
.qr-content section div{
    -webkit-box-shadow: none !important;
            box-shadow: none !important;
    border: solid 100px rgba(0, 0, 0, 0.479) !important;
    /* border: solid 30px green !important; */
}
.back-btn {
    position: absolute;
    left: 3vw;
    top: 3vw;
}

.message {
    color: #fff;
    font-family: Roboto;
    font-size: 20pt;
    padding-top: 10px;
}
.message.warning{
    color: #f7e300;
}


