.loader {
    border: 5px solid #ccc;
    border-radius: 50%;
    border-top: 5px solid #fddc62;
    width: 30px;
    height: 30px;
    -webkit-animation: spin 2s linear infinite; /* Safari */
    animation: spin 0.5s linear infinite;
  }

  @-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }

  @keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }

.loader-content{
    width: 100%;
    height: 100vh;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
    display: -ms-flexbox;
    display: flex;
}

.content-confirm {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

.header-bar {
  margin: 50px 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  float: left;
  width: 100%;
  padding: 0 70px;
}

button.confirm-btn{
  float: right;
  background: #48AD70;
  height: 86px;
  width: 250px;
  font-size: 33px;
  font-family: 'Roboto';
  color: #fff;
  border: none;
  -webkit-box-shadow: 0 2px 2px rgba(0, 0, 0, 0.25);
          box-shadow: 0 2px 2px rgba(0, 0, 0, 0.25);
  cursor: pointer;
}

.retry{
  width: unset !important;
  float: unset !important;
  font-size: 23px !important;
  height: 78px !important;
  padding: 0 25px !important;
  margin-top: 50px;
}

.confirm-data{
  position: fixed;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  text-align: center;
}

.confirm-data h2 {
  color: #0A6CB5;
  font-size: 32px;
  padding: 0;
  margin: 0;
  margin-bottom: 16px;
}

.confirm-data p {
  font-weight: bold;
  margin: 0;
  color: #989898;
  font-size: 32px;
  margin-bottom: 15px;
}

.confirm-data img{
  -webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.94);
          box-shadow: 0 3px 6px rgba(0, 0, 0, 0.94)
}


